
.resultsContainer{
    @apply w-full m-auto mt-12 mb-10 align-middle border-2 border-gray-400 rounded-md md:w-2/3;
}

.resultBtn{
    @apply h-8 m-2 text-center text-white bg-blue-500 border-2 border-blue-200 rounded-full;
}

.resultTitle{
    @apply h-8 pl-4 font-bold text-gray-700 bg-gray-200 rounded-t-md;
}

.optionResult{
    @apply flex flex-col gap-2 p-2 m-2 border-2 border-gray-300 rounded-md;
}

.choosenVote{
    @apply flex justify-center gap-2 border-2 border-blue-700 rounded-md w-28;
}