.searchIcon{
    @apply flex justify-center w-8 h-8 mt-6 transition border-2 border-gray-500 rounded-full cursor-pointer justify-self-center hover:border-yellow-200;
}

.searchInput{
    @apply h-8 px-3 py-3 rounded-lg ring-2 ring-gray-300 focus:ring-2 focus:ring-blue-600 focus:outline-none;
}

.resultDiv{
    @apply grid grid-cols-2 gap-5 border-2 border-gray-200 justify-items-center;
}

.pollDiv{
    @apply grid w-full grid-rows-2 gap-4 text-center border-2 border-gray-500 rounded-lg;
}

.resultLink{
    @apply w-2/3 h-8 mb-2 text-center text-white bg-blue-500 border-2 border-blue-200 rounded-full justify-self-center;
}

