.setItems-content{
    @apply absolute w-full rounded-md bottom-14 sm:w-1/2 md:w-2/3;
    animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
}

.popup-overlay{
    @apply bg-gray-200 bg-opacity-50
}

@keyframes anvil {
    0% {
      transform: scale(1) translateY(0px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }
    1% {
      transform: scale(0.96) translateY(10px);
      opacity: 0;
      box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }
    100% {
      transform: scale(1) translateY(0px);
      opacity: 1;
      box-shadow: 0 0 500px rgba(241, 241, 241, 0);
    }
  }

/* Form CSS */

.popupInput{
  @apply h-8 px-3 py-3 rounded-lg ring-2 ring-gray-300 focus:ring-2 focus:ring-blue-600 focus:outline-none;
}

.popupImageInput{
  @apply h-8 px-3 rounded-lg ring-2 ring-gray-300 focus:ring-2 focus:ring-blue-600 focus:outline-none;
}