
.votePollContainer{
    @apply grid w-10/12 mt-4 mb-4 border-2 rounded-lg justify-items-center;
}

.authorImage{
    @apply w-3/4 col-span-1 mt-2 ml-2 border-2 border-blue-200 rounded-full;
}

.pollInfoDiv{
    @apply grid w-10/12 col-span-2 ml-4 border-l-2 border-gray-400 justify-items-center;
}

.optionText{
    @apply flex items-center w-2/3 gap-5 border-2 rounded-lg hover:text-blue-500 hover:border-blue-500;
}

.submitBtn{
    @apply w-2/3 h-8 text-white bg-blue-500 border-2 border-blue-200 rounded-full justify-self-center;
}