
.newPollContainer{
    @apply w-full m-auto mt-12 mb-10 align-middle border-2 border-gray-400 rounded-md md:w-2/3;
}

.newPollTitle{
    @apply w-full h-8 text-lg font-bold text-center text-gray-700 bg-gray-200 rounded-t-md;
}

.newPollInput{
    @apply h-8 px-3 py-3 rounded-lg focus:ring-2 ring-2 ring-gray-300 focus:ring-blue-600 focus:outline-none;
}