
.authorInfo{
    @apply inline-flex w-full h-8 pl-4 bg-gray-200 rounded-t-md;
}

.authorName{
    @apply font-bold text-gray-700 transition hover:text-yellow-50;
}

.authorImg{
    @apply w-3/4 col-span-1 mt-2 ml-2 border-2 border-blue-200 rounded-full;
}

.pollContent{
    @apply grid w-10/12 col-span-2 border-l-2 border-gray-400 justify-items-center;
}

.pollBtn{
    @apply w-3/4 h-8 mb-2 text-white bg-blue-500 border-2 border-blue-200 rounded-full justify-self-center;
}